<template>
	<div id="find" @click='closeHeaderPop'>
		<we-header title='发现' ref='weHeader'></we-header>
		<ul class='findList'>
			<li class='findFirst' @click='$router.push("friendsCircle")'>
				<img src="~img/images/find/pyq.png" alt="">
				<p>朋友圈</p>
				<img src="https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2013897178,71148017&fm=58" alt="" class='remind'>
				<span class='remindPoint'></span>
			</li>
			<li>
				<img src="~img/images/find/sao.png" alt="">
				<p class='sao'>扫一扫</p>
			</li>
			<li @click='demo()'>
				<img src="~img/images/find/yao.png" alt="">
				<p>摇一摇</p>
			</li>
			<li @click='showDialog=true'>
				<img src="~img/images/find/fujin.png" alt="">
				<p>附近的人</p>
			</li>
			<li>
				<img src="~img/images/find/shop.png" alt="">
				<p>购物</p>
			</li>
			<li>
				<img src="~img/images/find/xcx.png" alt="">
				<p>小程序</p>
			</li>
		</ul>
		<van-dialog
			v-model="showDialog"
			title='提示'
			show-cancel-button
			:close-on-click-overlay='true'
			>
			查看附近的人功能将获取你的位置信息，你的位置信息会被保留一段时间。通过列表右上角的清除功能可随时手动清除位置信息。
			<van-checkbox v-model='checked' shape='square'>下次不提示</van-checkbox>
		</van-dialog>
	</div>
</template>
<script>
	import weHeader from './we-header'
	export default{
		name:'find',
		data(){
			return{
				showDialog:false,
				checked:false
			}	
		},
		methods:{
			closeHeaderPop(){
				this.$refs.weHeader.isShow=false;
			},
			demo(){
				console.log(1);
			}
		},

		activated() {
			this.closeHeaderPop();
	    },
	    deactivated(){
	    },
		components:{
			'we-header':weHeader 
		},
		beforeRouteLeave (to, from, next) {
			if(this.$refs.weHeader.isShow ){	
				this.closeHeaderPop();		
				next(false);
		    }	
		    else{
		    	next();
		    }	
		},

	}
</script>
<style scoped>
	#find{
		background: #ededed;
		/*padding-bottom: 2.2rem;*/
		height: 86vh;
	}
	.findList{
		margin-top: .8rem;
		margin-bottom: 1.5rem;	
	}
	.findList>li{
		background: #fff;
		margin-bottom: .26rem;
		padding:.3rem;
		display: flex;
		align-items: center;
		background: #fff url('~img/images/right.png') no-repeat 92vw center/.3rem;
	}
	.findList>li:first-child{
		padding-top: .4rem;
		padding-bottom: .24rem;
	}
	.findList>li>img{
		width: .58rem;
    	height: .58rem;
	}
	.findList>li>p{
		font-size: .38rem;
    	padding-left: .2rem;

	}
	.findList>li:nth-of-type(2){
		margin:0;
	}
	
	.findFirst{
		position: relative;
	}
	.findList .remind{
		position: absolute;
    	right: 10%;
	}
	.findList .remindPoint{
		display: inline-block;
	    width: .2rem;
	    height: .2rem;
	    background: #fa5251;
	    border-radius: 50%;
	    position: absolute;
	    right: .67rem;
    	top: .3rem;
	}
	p.sao:before{
		display: block;
		content: '';
		border-bottom:1px solid rgba(233,233,233,.6);
		width: 86vw;
		position: relative;
		top:.8rem;
	}
	.van-dialog {
		border-radius: .12rem !important;
	}
	#find >>> .van-dialog__header {
		font-size: .38rem;
	}
	#find >>> .van-dialog__content {
		font-size: .34rem;
		padding: .4rem;
	}
	#find >>>  .van-dialog .van-button ,#find >>> .van-dialog__header{
		font-weight: bold;
		font-size: .39rem;
	}
	.van-checkbox {
		padding:.3rem 0;
	}
	#find >>> .van-checkbox__icon--checked .van-icon {
		background: #08c261;
		border-color: #08c261;
	}
</style>


